.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: linear-gradient(180deg, $color-black-pearl 84.9%, rgba(4, 24, 39, 0) 100%);
  color: $color-default-white;

  @include vp-1023 {
    position: absolute;
  }
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 35px;

  @include vp-1023 {
    padding-top: 20px;
    padding-bottom: 25px;
  }

  @include vp-767 {
    padding-top: 10px;
    padding-bottom: 20px;
  }
}

.header__wrapper img {
  @include vp-767 {
    width: 35px;
    height: 35px;
  }

  @include hover {
    opacity: 0.5;
  }
}


.header__list {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 10px;
  margin-left: 15px;
  grid-column-gap: 18px;
  align-items: center;
  max-width: 480px;

  @include vp-1279 {
    justify-items: end;
  }

  @include vp-1023 {
    display: flex;
    flex-wrap: wrap;
    max-width: 400px;
    width: auto;
  }

  @include vp-767 {
    margin-right: 10px;
  }
}

.header__item {
  margin-bottom: 5px;

  &:last-child {
    margin-right: 0;
  }

  @include vp-1279 {
    margin-right: 20px;
  }

  @include vp-1023 {
    margin-right: 0;
    margin-bottom: 15px;
  }

  @include vp-767 {
    margin-bottom: 0;
  }
}

.header__link {
  font-family: $ff-mono;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: $color-default-white;
  display: flex;
  align-items: center;
  cursor: pointer;

  @include hover {
    color: $color-deep-sky-blue;
  }

  @include focus {
    outline: 1px solid $color-deep-sky-blue;
    border-radius: 2px;
  }

  @include active {
    opacity: 0.8;
  }

  @include vp-1023 {
    padding: 10px;
  }
}

.header__link-text {
  margin-left: 15px;

  @include vp-1023 {
    display: none;
  }
}

.header__button {
  @include vp-1023 {
    display: none;
  }
}
