@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  display: block;
  color: #000000;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:focus {
  box-shadow: inset 0 0 0 1000px rgba(4, 20, 39, 0.1490196078);
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #ffffff;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 24px;
  }
}

.about {
  background: #f8fdff;
  color: #041827;
  padding-bottom: 118px;
}
@media (max-width: 1023px) {
  .about {
    padding-bottom: 79px;
  }
}
@media (max-width: 767px) {
  .about {
    padding-bottom: 76px;
  }
}

.about__wrapper {
  margin-left: 250px;
}
.about__wrapper::before {
  position: absolute;
  content: "";
  width: 150px;
  height: 320px;
  top: 0;
  left: 50px;
  background-image: image-set(url("../img/year.webp") 1x, url("../img/year.png") 1x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .about__wrapper::before {
    background-image: image-set(url("../img/year@2x.webp") 1x, url("../img/year@2x.png") 1x);
  }
}
@media (max-width: 1023px) {
  .about__wrapper::before {
    top: 12px;
    left: 45px;
  }
}
@media (max-width: 767px) {
  .about__wrapper::before {
    display: none;
  }
}
@media (max-width: 1023px) {
  .about__wrapper {
    margin-left: 175px;
    padding: 10px 0 0;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    margin-left: 0;
  }
}

.about__wrapper h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
}
@media (max-width: 767px) {
  .about__wrapper h2 {
    font-size: 28px;
    line-height: 39px;
  }
}

.about__inner {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
  margin-bottom: 20px;
  max-height: 150px;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .about__inner {
    margin-top: 9px;
    max-height: 260px;
  }
}
@media (max-width: 767px) {
  .about__inner {
    max-height: 210px;
    margin-top: 12px;
  }
}

.about--more .about__inner {
  max-height: 100%;
  overflow: visible;
}

.about__inner p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 20px;
}
.about__inner p:last-child {
  margin-bottom: 0;
}
@media (max-width: 1023px) {
  .about__inner p {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .about__inner p {
    font-size: 14px;
    line-height: 19px;
  }
}

.about__btn-collapse {
  display: none;
}

.about--more .about__btn-collapse {
  display: block;
}

.about--more .about__btn-more {
  display: none;
}

.features {
  background: #f8fdff;
  padding-top: 120px;
  padding-bottom: 118px;
}
@media (max-width: 1023px) {
  .features {
    padding-top: 80px;
    padding-bottom: 70px;
  }
}
@media (max-width: 767px) {
  .features {
    padding-top: 50px;
    padding-bottom: 36px;
  }
}

.features__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
@media (max-width: 1023px) {
  .features__list {
    gap: 27px 30px;
  }
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: 1fr;
    gap: 29px 30px;
  }
}

.features__item {
  padding: 40px 45px 35px 40px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  min-height: 300px;
}
@media (max-width: 1023px) {
  .features__item {
    padding: 38px 32px 30px 36px;
  }
  .features__item:nth-child(n+3) {
    min-height: 327px;
  }
  .features__item:nth-child(n+3) h3 {
    margin-top: 13px;
    margin-bottom: 13px;
  }
}
.features__item:hover {
  background-color: #00b2ff;
  cursor: default;
}
.features__item:hover svg {
  fill: #f2f5f6;
}
.features__item:hover h3, .features__item:hover p {
  color: #f2f5f6;
}
@media (max-width: 767px) {
  .features__item {
    padding: 30px 30px 35px 25px;
    min-height: 340px;
  }
}

.features__item svg {
  fill: #00b2ff;
}
@media (max-width: 767px) {
  .features__item svg {
    width: 30px;
    height: 30px;
  }
}

.features__item h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
  margin-top: 11px;
  margin-bottom: 13px;
}
@media (max-width: 1023px) {
  .features__item h3 {
    margin-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .features__item h3 {
    margin-bottom: 9px;
    margin-top: 8px;
    font-size: 20px;
    line-height: 28px;
  }
}

.features__item p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
}
@media (max-width: 767px) {
  .features__item p {
    font-size: 14px;
    line-height: 19px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 3px;
  color: #f2f5f6;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    outline: 2px solid #00b2ff;
  }
}
.btn.focus-visible:focus {
  outline: 2px solid #00b2ff;
}
.btn--header {
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  margin-bottom: 5px;
  margin-right: 60px;
  color: #f2f5f6;
  background-color: rgba(255, 255, 255, 0);
  min-height: 40px;
  max-width: 160px;
  align-items: center;
}
.btn--header:hover {
  border-color: #00b2ff;
  color: #00b2ff;
  outline: none;
}
@media (max-width: 1279px) {
  .btn--header {
    margin-right: 0;
  }
}
.btn--hero {
  width: 410px;
  min-height: 55px;
  padding: 15px 25px;
  background-color: #00b2ff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .btn--hero {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .btn--hero {
    padding: 12px 38px;
    min-width: 270px;
    width: 100%;
    min-height: 40px;
    font-size: 14px;
    line-height: 16px;
    justify-content: center;
    margin-left: 0;
  }
}
.btn--about {
  margin-top: 20px;
  padding: 15px 50px;
  background-color: rgba(255, 255, 255, 0);
  color: #00b2ff;
  justify-content: center;
  border: 1px solid #00b2ff;
  text-decoration: none;
}
@media (max-width: 1023px) {
  .btn--about {
    margin-top: 0;
    padding: 13px 50px;
  }
}
@media (max-width: 767px) {
  .btn--about {
    padding: 8px 40px;
    margin-top: -3px;
  }
}
@media (hover: hover) {
  .btn--about:hover:not(.focus-visible) {
    background-color: #00b2ff;
    outline: none;
    color: #ffffff;
  }
}
.btn--about.focus-visible:focus {
  background-color: #00b2ff;
  outline: none;
  color: #ffffff;
}

.contacts {
  position: relative;
}

.contacts__title {
  margin: 0 0 22px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .contacts__title {
    margin-bottom: 0;
    margin-right: 30px;
  }
}

@media (max-width: 767px) {
  .is-opened .contacts__list {
    display: block;
  }
  .is-closed .contacts__list {
    display: none;
  }
  .is-nojs .contacts__list {
    display: grid;
  }
}
.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .contacts__list {
    margin-top: 20px;
  }
}

.contacts__item:not(:last-child) {
  margin-bottom: 9px;
}
@media (max-width: 767px) {
  .contacts__item:not(:last-child) {
    margin-bottom: 6px;
  }
}

@media (max-width: 767px) {
  .contacts__item:first-child {
    margin-bottom: 15px;
  }
}

.contacts__address,
.contacts__phone {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
@media (hover: hover) {
  .contacts__address:hover:not(.focus-visible),
.contacts__phone:hover:not(.focus-visible) {
    color: #00b2ff;
  }
}
.contacts__address.focus-visible:focus,
.contacts__phone.focus-visible:focus {
  outline: 1px solid #00b2ff;
  border-radius: 2px;
}
.contacts__address.focus-visible:active,
.contacts__phone.focus-visible:active {
  opacity: 0.8;
}

@media (max-width: 767px) {
  .contacts__address {
    font-size: 14px;
    line-height: 19px;
  }
}

@media (max-width: 767px) {
  .contacts__phone {
    font-size: 14px;
    line-height: 25px;
  }
}

@media (hover: hover) {
  .contacts__phone:hover:hover:not(.focus-visible) {
    color: #00b2ff;
  }
}

.feedback {
  position: relative;
  width: 100%;
  color: #ffffff;
  background-color: #00b2ff;
  min-width: 320px;
  z-index: 1;
}
.feedback::after {
  content: "";
  background: linear-gradient(to right bottom, rgba(4, 24, 39, 0.5) 50%, #00b2ff 50%, #00b2ff 100%);
  top: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  width: 50%;
  z-index: -1;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .feedback::after {
    width: 0;
    height: 0;
  }
}

.feedback__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 165px 31px 250px;
  max-width: 1280px;
}
@media (max-width: 1023px) {
  .feedback__wrapper {
    padding: 45px 50px 125px;
  }
}
@media (max-width: 767px) {
  .feedback__wrapper {
    padding: 48px 25px 83px;
  }
}

.feedback__image-wrapper {
  padding: 15px 0 0 0;
  align-self: center;
  margin-right: 20px;
}
@media (max-width: 1023px) {
  .feedback__image-wrapper {
    display: none;
  }
}

.feedback__content {
  display: flex;
  flex-direction: column;
