.form label input[type=checkbox] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.form label input[type=checkbox] + span::after {
  content: "";
  position: absolute;
  top: 25%;
  left: 11px;
  width: 15px;
  height: 15px;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #ffffff;
  border-radius: 2px;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .form label input[type=checkbox] + span::after {
    left: 0;
  }
}
@media (max-width: 767px) {
  .form label input[type=checkbox] + span::after {
    left: 5px;
    top: 12px;
  }
}

.form label input[type=checkbox]:focus + span::after {
  content: "";
  border: 1px solid #000000;
}

.form label input[type=checkbox]:checked + span::before {
  content: "";
  position: absolute;
  top: 35%;
  left: 5%;
  width: 9px;
  height: 6px;
  background-image: url("../img/svg/ckeck-mark.svg");
}
@media (max-width: 1023px) {
  .form label input[type=checkbox]:checked + span::before {
    left: 1%;
  }
}
@media (max-width: 767px) {
  .form label input[type=checkbox]:checked + span::before {
    left: 3%;
  }
}

.form label input[type=checkbox] ~ span:nth-of-type(2n) {
  display: inline-block;
  vertical-align: bottom;
  padding-left: 37px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 1023px) {
  .form label input[type=checkbox] ~ span:nth-of-type(2n) {
    padding-left: 24px;
    max-width: 355px;
    margin-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .form label input[type=checkbox] ~ span:nth-of-type(2n) {
    margin-top: 6px;
    padding-left: 30px;
    max-width: none;
  }
}

.form label input[type=checkbox]:hover + span::after,
.form label input[type=checkbox]:hover ~ span:nth-of-type(2n) {
  cursor: pointer;
  opacity: 0.8;
}

.form--popap {
  grid-template-columns: 1fr;
