.services__item--first {
  background-position: 15% 0;
}
.services__item--second {
  background-position: 47% 0;
}
.services__item--third {
  background-position: 78% 0;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .services__item {
    background-image: image-set(url("../img/main-block@2x.webp") 2x, url("../img/main-block@2x.jpg") 2x);
  }
}
.services__item::after {
  content: "";
  position: absolute;
  background: rgba(4, 24, 39, 0.5);
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
}
.services__item::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  background: #00b2ff;
  background-image: url("../img/svg/arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
  padding: 10px;
  height: 60px;
  min-width: 60px;
}
.services__item:hover, .services__item:focus {
  opacity: 0.8;
  box-shadow: 10px -10px 10px #00b2ff;
}
.services__item:hover::before, .services__item:focus::before {
  border-color: #00b2ff;
}
.services__item:active {
  box-shadow: 0 0 10px #ffffff;
}
@media (max-width: 1023px) {
  .services__item {
    width: 100%;
    min-height: 250px;
    background-size: 1300px 660px;
  }
  .services__item--first {
    background-position: 12% 100%;
  }
  .services__item--second {
    background-position: 80% 60%;
  }
  .services__item--third {
    background-position: 12% 70%;
  }
  .services__item::after {
    min-height: 250px;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .services__item {
    min-width: 270px;
  }
  .services__item--first {
    background-position: 30% 55%;
  }
  .services__item--second {
    background-position: 30% 100%;
  }
  .services__item--third {
    background-position: 75% 73%;
  }
  .services__item::before {
    min-width: 40px;
    height: 40px;
  }
}

.services__link {
  color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  padding: 50px 40px;
  overflow: auto;
}
@media (max-width: 1023px) {
  .services__link {
    padding: 38px 45px;
  }
}
@media (max-width: 767px) {
  .services__link {
    padding: 24px 30px;
  }
}

.services__link h3 {
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 20px;
  line-height: 30px;
  width: 151px;
}
@media (max-width: 1023px) {
  .services__link h3 {
    width: 150px;
  }
}
@media (max-width: 767px) {
  .services__link h3 {
    font-size: 18px;
    line-height: 30px;
    width: 130px;
  }
}

.social {
  max-width: 215px;
}
@media (max-width: 1023px) {
  .social {
    display: flex;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .social {
    flex-direction: column;
    align-items: center;
  }
}

.social__logo-wrapper {
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .social__logo-wrapper {
    margin: 0;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .social__logo-wrapper {
    margin: 0 0 15px;
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 0 10px;
  padding: 0;
  list-style: none;
  width: 165px;
}
@media (max-width: 1023px) {
  .social__list {
    padding: 5px 0 0;
    width: 150px;
  }
}
@media (max-width: 767px) {
  .social__list {
    margin: 0 0 0 4px;
    padding: 0;
    justify-content: center;
  }
}

.social__item {
  display: flex;
  width: 40px;
  height: 40px;
  padding: 5px;
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .social__item:last-child {
    margin: 0;
  }
}

.social__link svg {
  fill: #f8fdff;
}
@media (hover: hover) {
  .social__link svg:hover:not(.focus-visible) {
    opacity: 0.8;
    fill: #00b2ff;
  }
}
.social__link svg.focus-visible:focus {
  opacity: 0.8;
  fill: #00b2ff;
}

.toggle {
  display: none;
}
@media (max-width: 767px) {
  .toggle {
    display: block;
    position: absolute;
    top: 16px;
    right: 22px;
    width: 25px;
    height: 25px;
    box-sizing: border-box;
    outline: none;
    border: none;
    background-color: rgba(255, 255, 255, 0);
  }
}

@media (max-width: 767px) {
  .is-opened .toggle::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    top: 11px;
    right: 4px;
  }
  .is-closed .toggle::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    top: 11px;
    right: 4px;
  }
  .is-closed .toggle::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    top: 11px;
    right: 4px;
    transform: rotate(90deg);
  }
  .is-nojs .toggle {
    display: none;
  }
  .toggle:focus {
    color: #ffffff;
    outline: 1px solid #00b2ff;
    border-radius: 2px;
  }
}
